export default {
	data() {
		return {
			name:'',
			mobile: '',
			code: '',
			state: false, //是否开启倒计时,
			time: 70, //总时间，单位秒
			getCodeTxt: '获取验证码'
		}
	},
	onLoad(options) {
		console.log(options)
	},
	methods: {
		toLogin() {
			uni.redirectTo({
				url: "./signUp"
			})
		},
		//点击获取验证码
		getCode() {
			if (this.mobile == '') {
				this.showToast('请输入手机号码')
				return
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/User/sendActiveCode',
				data: {
					phone: this.mobile
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.showToast('验证码已发送')
						//开始倒计时
						this.state = true;
						//执行倒计时
						this.checkingTime();
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//倒计时
		checkingTime() {
			var _this = this
			var timer = setInterval(function() {
				_this.time--
				if (_this.time <= 0) {
					clearInterval(timer)
					_this.state = false;
					_this.getCodeTxt = '重新获取'
					_this.time = 70
				}
			}, 1000)
		},
		//手机验证码登录
		login() {
			if(this.name==""){
				this.showToast('请输入会员名')
				return
			}
			if (this.mobile == '') {
				this.showToast('请输入手机号码')
				return
			} else if (this.code == '') {
				this.showToast('请输入验证码')
				return
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/User/loginByActiveCode",
				data: {
					name:this.name,
					phone: this.mobile,
					active_code: this.code
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
				}
			})
		}
	}
}
